.main-nav {
  display: flex;  
  list-style: none;
  justify-content: flex-end;
  background-color:rgba(133, 133, 133, 0.6);
  opacity:0.8;
  font-size: calc(14px + 8*(100vw - 300px)/1300);
  margin: 0;
}

li {
  padding: 20px;
  color: white;  
  font-weight: bold;
}

.link {
  text-decoration: none;
}

.push{
  margin-right: auto;
  color:rgb(2, 2, 41);
}

