/* homepage ************************* */


.cover {
  width: 100%;
  height: auto;
  
}

.background-container {
  position: relative;
  text-align: center;
  }

.centered {
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-weight: bold;
  font-size: calc(32px + 8*(100vw - 300px)/1300);
  font-family: source-code-pro,Menlo,Monaco,Consolas,Courier New,monospace;
}

.centered-below {
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: calc(23px + 8*(100vw - 300px)/1300);

  font-family:  source-code-pro,Menlo,Monaco,Consolas,Courier New,monospace;
}

.title {
  color: white;
  
  padding: 10px;
}

.title-box {
  display: flex;
  flex-direction: column;  
}

.title:hover {
  text-shadow: 2px;
}


@media only screen and (max-width: 600px) {
  .centered {
    position: absolute;
    top: 10%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .centered-below {
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .title {
    /* font-size: 1em; */
    padding: 5px;
  }
}



/* About **************************** */

#about{
  text-align: center;
  padding: 30px;
  margin: 30px 120px;
}

.about-header{
  padding: 10px;
}

@media only screen and (max-width: 900px) {
  #about{
    padding: 20px;
    margin: 30px 50px;
  }
}

@media only screen and (max-width: 600px) {
    #about{
      padding: 10px;
      margin: 30px 20px;
    }
  }


/* Projects   ************************** */

#projects{
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color:rgba(133, 133, 133, 0.6);  
}

.projectsHeader {
  margin:10px;
  color: white;
}

.projectsGrid {
  display: flex;
  /* grid-gap: 25px;
  grid-template-columns: repeat(autofill, minmax(200, 1fr));
  grid-template-rows: 1fr; */
}

.projectsGridItem {
  display: flex;
  flex-direction: column;
  text-align: center;
  border: solid 10px rgba(2, 2, 41, 0.856);
}

.projectImage{
  margin: 100px 150px; 

  transition: box-shadow 1s;
}

.projectImage:hover{
  box-shadow: 0px 20px 20px 0px;
}

.projects-description{
  padding: 5px;
  margin: 10px;
}

.projects-link{
  padding: 5px;
  margin: 10px 10px 20px 10px;
}

@media only screen and (max-width: 600px) {
  .projectImage{
    margin: 10px 25px;   
  }
}

  @media only screen and (max-width: 850px) {
    .projectImage{
      margin: 10px 20px;   
    }
  }

  /* @media only screen and (max-width: 1000px) {
    .projectImage{
      margin: 10px 120px;   
    }
  } */



/* Contact */

#contact{
  height: 130px;
  margin: 10px;
  padding: 30px;
}

.formContainer{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  margin: 30px 150px;
  border: solid 3px;  
}

.emailForm, .messageForm {
  padding: 3px;
  font-family: Helvetica;
  text-align: center;
}

button {
  padding: 8px 12px 8px 12px;
  margin: 10px;
  background-color: rgb(49, 49, 241);
  border-radius: 15px;
  color: white;  
}



